import { Injectable } from "@angular/core";
import { Pelicula } from "../models/pelicula";


@Injectable()
export class PeliculaService{
    public peliculas:Pelicula[];
    constructor(){
        this.peliculas=[  new Pelicula('Spiderman 4',2019,'https://poptv.orange.es/wp-content/uploads/sites/3/2020/08/spider-man-portada.jpg'),
        new Pelicula('los Vengadores',2018,'https://i0.wp.com/hipertextual.com/wp-content/uploads/2019/04/hipertextual-mejores-trailers-semana-avengers-endgame-rey-leon-godzilla-2-2019907932.jpg?fit=1600%2C900&ssl=1'),
        new Pelicula('Batman vs Superman',2015,'https://www.comicverso.com/wp-content/uploads/2019/12/destacada-batmanvssuperman.jpg'),];
    }
    holaMundo(){
        return "Hola desde el servicio de Angular"
    }
    getPeliculas(){
        return this.peliculas;
    }
}


